.contact-edit {
  background-color: rgb(242, 231, 189);
  justify-content: space-around;
  padding: 50px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  background-color: rgb(242, 231, 189);
  font-size: 20px;
  padding: 30px;
  position: relative;
  @include for-mobile-layout {
    padding: 7px 30px;
  }
  img {
    @include for-mobile-layout {
      width: 150px;
    }
  }
  h1 {
    font-size: 25px;
  }
  form {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
    align-items: center;
    @include for-mobile-layout {
      grid-column: 1;
    }
    input {
      height: 50px;
      border: 1px solid rgb(218, 218, 218);
      border-radius: 5px;
      padding: 10px;
      width: 30%;
      &:focus {
        outline: 2px solid gray;
        border: none;
      }
      @include for-mobile-layout {
        width: 100%;
      }
    }
    .edit-btns {
      margin-top: 10px;
      button {
        display: flex;
        padding: 4px 8px;
        background-color: rgb(252, 213, 53);
        color: rgb(10, 10, 12);
        border-radius: 5px;
        font-size: 15px;
        text-decoration: none;
        font-family: PoppinsMedium;
        border: none;
        cursor: pointer;
      }

      .edit-back {
        text-decoration: none;
        color: black;
        border-radius: 5px;
          position: absolute;
          left: 0;
          top: 0;
          display: flex;
          padding: 4px 8px;
          background-color: rgb(252, 213, 53);
          color: rgb(10, 10, 12);
          border-radius: 5px;
          font-size: 15px;
          text-decoration: none;
          font-family: PoppinsMedium;
          border: none;
          cursor: pointer;
        
      }
    }
  }
}
